import React, { Component } from "react";
import { Link } from "react-router-dom";
import CoursesData from '@data/courses';
import StarValue from "@components/StarValue";
import UdemySvg from '@assets/svg/udemy.svg';

import './index.scss';
import { connect } from "react-redux";
import Course from "@components/Course";
export const urlFromItem = (item: typeof CoursesData[0]) => {
    const { url, referralCode, couponCode } = item;
    const referralStr = referralCode
        ? ("referralCode=" + referralCode + "&")
        : "";
    const couponStr = couponCode
        ? ("couponCode=" + couponCode)
        : "";
    return `${url}?${referralStr}${couponStr}`;
}

const sWidth: number = window.innerWidth;

const mapStateToProps = (state: any) => ({
    cms: state.cms
})

const mapActionsToProps = {
}


const Courses: React.FC<any> = (props) => {
    return (
        <section className="courses" id="courses">
            {!props?.hideTitle && (
                <h2 className="courses-header">
                    <img src={UdemySvg}
                        width="128px" height="58px"
                        style={{ marginRight: "5px" }}
                    />
                </h2>)
            }
            <div className="container-fluid top-container">
                <div className="courses container" style={{
                    ...props?.hideTitle && (
                        sWidth < 992 ? {
                            marginTop: "-150px",
                        } : {
                            marginTop: "-148px"
                        }
                    )
                }}>
                    {props?.cms?.courses?.map((course, index) => {
                        //return renderOneCourse(course)
                        return <Course item={course} key={index} />
                    })}
                </div>
            </div>
        </section>
    )
}

export default connect(mapStateToProps, mapActionsToProps)(Courses);